.skill {
    margin: 5px;
    width: 250px;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px 0;
}

.icon {
    border-radius: 10px;
    width: 80px;
    height: 80px;
    border: 1px solid;
    object-fit: cover;
    background-color: white;

}